import React from "react";
import Helmet from "react-helmet";

import Layout from "components/Layout";
import Container from "components/Container";
import img_gatsby from "assets/images/about.jpg";

const AboutPage = () => {
  return (
    <Layout pageName="about">
      <Helmet>
        <title>About</title>
      </Helmet>
      <Container>
	<p className="cover">
          <img src={img_gatsby} alt="Carman Terblanche"/>
        </p>
        <h1>About</h1>
        <p>I am a freelance filmmaker based in Pretoria, South Africa. I graduated from Monash University
 with a degree in Computer and Information Sciences. It's a stretch from that to filmmaking, but I love telling stories
 and capturing great shots.</p>

<p>I was born in Johannesburg, South Africa and grew up in Pretoria for most my life.
I loved doing everything! From acting to sports to music and probably anything you can imagine.
 Finding a passion that sticks was difficult, so I tried combining them to make something new.
Eventually, after much trial and error, I ended up here. </p>
</Container>
    </Layout>
  );
};

export default AboutPage;
